@charset "UTF-8";
html {
  box-sizing: border-box; }

html,
body {
  height: 100%; }

* {
  box-sizing: inherit; }
  *::before, *::after {
    box-sizing: inherit; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  overflow-x: hidden; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

@font-face {
  font-weight: 700;
  font-family: 'Roboto';
  src: url("fonts/Roboto-Bold.ttf") format("ttf");
  font-display: swap; }

@font-face {
  font-weight: 500;
  font-family: 'Roboto';
  src: url("fonts/Roboto-Medium.ttf") format("ttf");
  font-display: swap; }

@font-face {
  font-weight: 400;
  font-family: 'Roboto';
  src: url("fonts/Roboto-Regular.ttf") format("ttf");
  font-display: swap; }

body.theme_dark {
  color: #fcfbf7; }
  body.theme_dark > div {
    background: radial-gradient(70.24% 70.24% at 49.87% 29.76%, rgba(18, 12, 1, 0.9) 0%, #000000 100%); }
  body.theme_dark .leaders__title {
    color: #fcfbf7; }
  body.theme_dark .leaders__subtitle {
    color: #918f8a; }
  body.theme_dark .leader__avatar {
    filter: grayscale(1); }
  body.theme_dark .leader__name {
    color: #fcfbf7; }
  body.theme_dark .leader__score {
    color: #bfbfbf; }
  body.theme_dark .leader__bar {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) -2.22%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 95.38% at 38.75% 71.48%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
    box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(103, 103, 103, 0.6); }
  body.theme_dark .leader--winner .leader__bar {
    background: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
    box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.4); }
  body.theme_dark .leader__bar > span {
    color: #fcfbf7; }
  @media (orientation: portrait) {
    body.theme_dark .leaders__item--choosen-user .leader__score {
      color: #fcfbf7; }
    body.theme_dark .leaders__item--choosen-user .leader__bar {
      background: none;
      box-shadow: none; }
      body.theme_dark .leaders__item--choosen-user .leader__bar::before {
        background-color: #918f8a; } }

body.theme_light {
  color: #261c00; }
  body.theme_light > div {
    background: #ffffff; }
  body.theme_light .leaders__title {
    color: #261c00; }
  body.theme_light .leaders__subtitle {
    color: #bfbfbf; }
  body.theme_light .leader__avatar {
    filter: grayscale(0); }
  body.theme_light .leader__name {
    color: #261c00; }
  body.theme_light .leader__score {
    color: #bfbfbf; }
  body.theme_light .leader__bar {
    background: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%, rgba(234, 234, 234, 0) 100%);
    box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(207, 207, 207, 0.5); }
  body.theme_light .leader--winner .leader__bar {
    background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
    box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.8); }
  body.theme_light .leader__bar > span {
    color: #261c00; }
  @media (orientation: portrait) {
    body.theme_light .leaders__item--choosen-user .leader__score {
      color: #261c00; }
    body.theme_light .leaders__item--choosen-user .leader__bar {
      background: none;
      box-shadow: none; }
      body.theme_light .leaders__item--choosen-user .leader__bar::before {
        background-color: #261c00; } }

.leaders {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 20px;
  padding-right: 24px;
  padding-left: 24px;
  text-align: left; }
  @media (orientation: landscape) {
    .leaders {
      text-align: center; } }

.leaders__title {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 30px;
  line-height: 32px;
  font-family: "Roboto", "Arial", sans-serif;
  margin-bottom: 8px; }

.leaders__subtitle {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  font-family: "Roboto", "Arial", sans-serif; }

.leaders__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 100%; }

.leaders__item-first-place {
  position: relative;
  z-index: 2;
  order: 2; }
  .leaders__item-first-place .leader__avatar-winner-emoji {
    position: absolute;
    z-index: 1;
    top: -22px;
    left: calc(50% - 5px);
    width: 32px;
    height: 32px;
    font-size: 32px;
    line-height: 32px;
    transform: translateX(-50%); }

.leaders__item-second-place {
  z-index: 1;
  order: 3;
  margin-left: -16px;
  text-align: right; }
  .leaders__item-second-place .leader {
    padding-left: 8px; }

.leaders__item-third-place {
  z-index: 1;
  order: 1;
  margin-right: -16px; }
  .leaders__item-third-place .leader {
    padding-right: 8px; }

.leaders__item-fourth-place {
  display: none; }

.leaders__item-fifth-place {
  display: none; }

.leaders__item--choosen-user {
  position: absolute;
  z-index: 3; }
  .leaders__item--choosen-user .leader__avatar-wrapper::before {
    content: 'ðŸ‘';
    position: absolute;
    z-index: 1;
    top: -22px;
    left: calc(50% - 5px);
    width: 32px;
    height: 32px;
    font-size: 32px;
    line-height: 32px;
    transform: translateX(-50%); }
  .leaders__item--choosen-user .leader__bar {
    position: relative;
    padding-top: 0;
    padding-bottom: 16px; }
    .leaders__item--choosen-user .leader__bar::before {
      content: '';
      position: absolute;
      top: -8px;
      left: 50%;
      width: 60px;
      height: 1px;
      transform: translateX(-50%); }

@media (orientation: landscape) {
  .leaders__item-first-place {
    order: 3; }
  .leaders__item-second-place {
    order: 4; }
  .leaders__item-third-place {
    order: 2; }
  .leaders__item-fourth-place {
    order: 5;
    display: block;
    margin-left: -16px; }
    .leaders__item-fourth-place .leader {
      padding-left: 8px; }
  .leaders__item-fifth-place {
    position: static;
    z-index: 0;
    display: block;
    order: 1;
    margin-right: -16px; }
    .leaders__item-fifth-place .leader {
      padding-right: 8px; }
    .leaders__item-fifth-place .leader__bar {
      position: static;
      padding-top: 16px;
      padding-bottom: 0; }
      .leaders__item-fifth-place .leader__bar::before {
        display: none; } }

.leader {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center; }

.leader__user {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 16px; }

.leader__avatar-wrapper {
  position: relative; }

.leader__avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%; }

.leader__name {
  margin: 0;
  padding: 0;
  max-width: 120px;
  margin-bottom: 4px;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  font-family: "Roboto", "Arial", sans-serif; }

.leader__score {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  font-family: "Roboto", "Arial", sans-serif; }

.leader__bar {
  display: block;
  width: 120px;
  padding-top: 18px;
  border-radius: 6px 6px 0 0; }

.leader__bar > span {
  font-weight: 700;
  font-size: 30px;
  line-height: 32px;
  font-family: "Roboto", "Arial", sans-serif; }
