.leaders {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 20px;
  padding-right: 24px;
  padding-left: 24px;
  text-align: left;

  @media (orientation: landscape) {
    text-align: center;
  }
}

.leaders__title {
  @include title;
  margin-bottom: 8px;
}

.leaders__subtitle {
  @include subtitle;
}

.leaders__list {
  @include clear-list;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
}

.leaders__item {
  &-first-place {
    position: relative;
    z-index: 2;
    order: 2;

    .leader__avatar-winner-emoji {
      position: absolute;
      z-index: 1;
      top: -22px;
      left: calc(50% - 5px);
      @include size(32px, 32px);
      font-size: 32px;
      line-height: 32px;
      transform: translateX(-50%);
    }
  }

  &-second-place {
    z-index: 1;
    order: 3;
    margin-left: -16px;
    text-align: right;

    & .leader {
      padding-left: 8px;
    }
  }

  &-third-place {
    z-index: 1;
    order: 1;
    margin-right: -16px;

    & .leader {
      padding-right: 8px;
    }
  }

  &-fourth-place {
    display: none;
  }

  &-fifth-place {
    display: none;
  }

  &--choosen-user {
    position: absolute;
    z-index: 3;

    .leader__avatar-wrapper::before {
      content: 'ðŸ‘';
      position: absolute;
      z-index: 1;
      top: -22px;
      left: calc(50% - 5px);
      @include size(32px, 32px);
      font-size: 32px;
      line-height: 32px;
      transform: translateX(-50%);
    }

    .leader__bar {
      position: relative;
      padding-top: 0;
      padding-bottom: 16px;

      &::before {
        content: '';
        position: absolute;
        top: -8px;
        left: 50%;
        @include size(60px, 1px);
        transform: translateX(-50%);
      }
    }
  }

  @media (orientation: landscape) {
    &-first-place {
      order: 3;
    }

    &-second-place {
      order: 4;
    }

    &-third-place {
      order: 2;
    }

    &-fourth-place {
      order: 5;
      display: block;
      margin-left: -16px;

      & .leader {
        padding-left: 8px;
      }
    }

    // &-fifth-place {

    // }

    &-fifth-place {
      position: static;
      z-index: 0;
      display: block;
      order: 1;
      margin-right: -16px;

      & .leader {
        padding-right: 8px;
      }

      .leader__bar {
        position: static;
        padding-top: 16px;
        padding-bottom: 0;

        &::before {
          display: none;
        }
      }
    }
  }
}
